import React, { Component } from 'react'
import Header from '../headerComponent/header'
import '../../Assets/css/activity.css'
import { Link } from 'react-router-dom'

class Activity extends Component {
	render() {
		return (
			<div className='page container-fluid animated fadeIn'>
				<Header />
				<div className='flex'>
				<div className='activity'>
					<ul className='activities'>
						<p className='heading animated slideInLeft'>Форми діяльності:</p>
						<li><p className='list-item'><span role='img'>&#x2714;</span> Блог (статті та відео форма)</p></li>
						<li><p className='list-item'><span role='img'>&#x2714;</span> Онлайн робота:</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> марафони</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> семінари</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> прямі трансляції</p></li>
						<li><p className='list-item'><span role='img'>&#x2714;</span> Індивідуальна консультація</p></li>
						<li><p className='list-item'><span role='img'>&#x2714;</span> Групова робота у вигляді:</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> тренінгів</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> психологічних  груп</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> ресурсно-психологічних груп</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> майстер-класів</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> семінарів</p></li>
						<li><p className='list-item'><span role='img'>&#x25AB;</span> воркшопів</p></li>
					</ul>
				</div>
				<div className='activity'>
					<ul className='activities'>
						<p className='heading animated slideInLeft'>Їжащо працює з питаннями:</p>
						<li><p className='list-item point'><span role='img'>&#x2754;</span> Що таке баланс у житті?</p>
						<div className='collapsible'>(професійне вигорання, мене не хватає для цього життя, хронічна втома, мене ніхто не підтримує, яку професію мені обрати, як змінити професію, як знайти золоту середину між роботою та сім’єю)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2754;</span> Що таке розуміти себе?</p>
						<div className='collapsible'>(неприйняття себе, нестабільна або низька самооцінка, «що я хочу, що я можу, що мені робити?», проблеми з самоповагою, пошук самого себе, нерішучість, почуття вини, сорому, самотність)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2754;</span> Що таке сенс життя?</p>
						<div className='collapsible'>(що я хочу, навіщо я живу, відсутність цілей, труднощі з прийняттям рішень)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2754;</span> Що таке стосунки?</p>
						<div className='collapsible'>(труднощі у спілкуванні, страх оцінки партнером, сімейні конфлікти, він/вона мене не розуміє, як мені дружити з людьми)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2754;</span> Що таке емоції?</p>
						<div className='collapsible'>(невміння інтерпретувати емоції, емоційне вигорання, контроль емоцій, не розуміння емоцій, неприйняття емоцій, емоційні перепади)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2754;</span> Що саме я боюся (страх, тривога, хвилювання)?</p>
						<div className='collapsible'>(відсутність сну, панічні атаки, незрозумілість хвилювання, постійна напруга у тілі, відчуття тривоги 24/7)
</div>
<Link to='/contacts'>
<button className='contactButton'>Мої контакти</button>
</Link>
</li>
					</ul>
				</div>
				<div className='activity'>
					<ul className='activities'>
						<p className='heading animated slideInLeft'>Чому саме Їжащо?</p>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Конфіденційність</p>
						<div className='collapsible'>(усе тільки між нами, як на особистісному так і на законодавчому рівні)</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Професіоналізм</p>
						<div className='collapsible'>(я кожний рік вступаю у новий терапевтичний проект та закінчую його, супервізуюся та чітко відслідковую свої межі компетентності. Коли я бачу, що Твій запит поза межами моєї компетенція, я з душевним спокоєм та теплом перескерую Тебе до іншого висококваліфікованого спеціаліста)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Організація часу</p>
						<div className='collapsible'>(я завжди знайду зручний для Тебе час, як очно так і онлайн)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Доступна вартість</p>
						<div className='collapsible'>(я завжди Тобі запропоную обрати найкращий та найкорисніший для Тебе варіант)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Індивідуальність</p>
						<div className='collapsible'>(iндивідуальність (Ти – це Ти! Ти – особлива! Ми шукаємо рішення задачі разом, опираючись лише на Твій досвід та на Твій світогляд)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Уважність</p>
						<div className='collapsible'>(я дуже прискіплива до деталей, бо прагну, щоб після нашої зустрічі Ти мала розуміння, що робити та як дивитися на ситуацію з новим поглядом)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Креативність</p>
						<div className='collapsible'>(цього у мене хоч відбавляй. Готова спробувати? Я повністю відкрита до Твоїх ідей)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Відкритість</p>
						<div className='collapsible'>(усі мої методи та техніки які я використовую, з залюбки можу пояснити та роз’яснити чому саме цю техніку ми використали. Просто питай)
</div></li>
						<li><p className='list-item point'><span role='img'>&#x2714;</span> Постійність</p>
						<div className='collapsible'>(я на зв’язку та на постійному місці роботи, завжди відкрита для клієнтів 24 на 6)
</div></li>
					</ul>
					<Link to='/contacts'>
						<button className='contactButton secondContactButton'>Мої контакти</button>
					</Link>
				</div>
				</div>
			</div>
		)
	}
}
export default Activity;