import React, { Component } from 'react'
import Header from '../headerComponent/header'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
// import { Link } from 'react-router-dom'
import '../../Assets/css/contacts.css'

class Contacts extends Component {
	render() {
		return (
			<div className='contacts__container animated fadeIn'>
				<Header />
				<p className='contacts__greeting'>
					Питання, відгуки, пропозиції (теми відео, статей), записатися на консультацію 
					або просто хочеш сказати привіт?
				</p>
				<p className='contacts__greeting'>
					Не соромся, надсилай мені електронний лист!
				</p>
				<form>
					<div className='contact-input'>
					<TextField
						id="outlined-basic"
						label="Твоє ім'я"
						margin="normal"
						variant="outlined"
						className="contact-input-value"
						required
					/>
					<TextField
						id="outlined-basic"
						label="Електронна адреса"
						margin="normal"
						variant="outlined"
						className="contact-input-value"
						type='email'
						required
					/>
  					</div>
  					<div className="form-group">
    					<label for="exampleFormControlTextarea1">Повідомлення</label>
    					<textarea className="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
  					</div>
					  {/* <button type="button" class="button btn btn-success">Полетіли!</button> */}
					<Button variant="outlined" color="primary" className='button' type='submit' onClick={(event) => {
						event.preventDefault()
					}}>
  						Полетіли!
      				</Button>
  					<hr />
  					<p className='contacts__text'>
						Підписуйся та будь в курсі найцікавіших тем!
						<span role='img' aria-label='email'> &#x1F4DC;</span>
					</p>
					<form action="https://facebook.us19.list-manage.com/subscribe/post?u=6ee76c62ae5d32f355fad3587&amp;id=7090286fd3" method="post" id="mc-embedded-subscribe-form" 
					name="mc-embedded-subscribe-form" target="_blank" noValidate>
					<Button variant="outlined" color="primary" className='button' type="submit">
  						Підпишись!
      				</Button>
					</form>
				</form>
			</div>
		)
	}
}
export default Contacts