import React, { Component } from 'react'
import '../../Assets/css/homepageFirst.css'
// import SomePic from '../../Assets/images/mainpagePic.jpg'
import Scroll from '../../Assets/images/scroll.png'

class HomepageFirst extends Component {
	scrollClick() {
		window.scrollBy(0, window.innerHeight);
	}
	render() {
		return (
			<div className='wrap'>
				{/* <img className="somePic" src={SomePic} alt="photo" /> */}
				<h1 class="heading heading--stroke heading--shadow">Їжащо<br/>це проект про щастя </h1>
				<p>Воно десь поряд</p>
				<img className="scrollImage" src={Scroll} alt="scroll" 
					onMouseEnter={this.scrollClick} />
			</div>
		)
	}
}
export default HomepageFirst