import React, { Component } from 'react'
import Header from '../headerComponent/header'
import MyPhoto from '../../Assets/images/myphoto.jpg'
import { Link } from 'react-router-dom'
import Particles from 'react-particles-js'
import Paper from '@material-ui/core/Paper';
import '../../Assets/css/aboutMe.css'

const particlesOptions = {
	particles: {
	  polygon: {
		draw: {
		  enable: true,
		  stroke: {
			width: 10
		  }
		}
	  },
	  number: {
		value: 50,
		density: {
		  enable: true,
		  value_area: 800
		}
	  }
	}
  }

class AboutMe extends Component {
	render() {
		return (
			<div className='animated fadeIn'>
				<Header />
				<div className='aboutMe__container'>
					<div className='photoSummary'>
						<Particles className="particles"
          					params={particlesOptions}
        				/>
						<img className='photo animated rollIn' src={MyPhoto} alt='Zlatoslava Phil photo' />
						<div className='summary'>
							<Paper className="paper">
								<p>
									Клінічний психолог, кандидат у психотерапевти за напрямками:
									гештальт-психотерапія та арт-терапія, ведуча трансформаційних ігор
								</p>
							</Paper>
						</div>
					</div>
					<div className='info'>
						<p className='info__paragraph animated bounceInLeft'>
							Привіт! Давай пятюню! <span role='img' aria-label='high-five'>&#x1F91A;</span>
						</p>
						<p className='info__paragraph'>
							Дякую ;)
						</p>
						<p className='info__paragraph'>
							Я вітаю Тебе (давай відкинемо формальності і будемо товаришувати). 
							Як і кожна дружба починається зі знайомства.
						</p>
						<p className='info__paragraph'>
							Дозволь мені розпочати!
						</p>
						<p className='info__paragraph'>
							Мене звати Златослава і я з наймилозвучнішого та найдушевнішого міста в Україні, зі Львова! 
							За освітою я клінічний психолог. Спитаєш мене: чому саме психологія? Я з дитинства завжди аналізувала, 
							де я можу застосувати усі свої якості: пунктуальність, акуратність, відповідальність, ініціативність, 
							цілеспрямованість, впевненість, раціональність та інтуїцію. Так, знаю, дуже скромно, про те - що природа 
							надала те й використовую! Я завжди хотіла допомагати людям і завжди знала, що буду лікувати. 
							З часом, пізнаючи світ на просторах науки, я зрозуміла, що хочу та можу лікувати душу! 
							Психологія, у першу чергу, це наука про душу, про те які ми всі одночасно різні і однакові. 
							І я почала вивчати. По закінченню школи, поступила на психолога (звичайно, мої батьки були дуже проти. 
								І тільки по закінченню університету з відмінними оцінками, вони як і мої вже на той час перші клієнти, 
								зрозуміли, я живу своєю професією). Більше про жодну професію я не думала, бо для мене, вибір професії 
							є одним і на усе життя. Чи важко було вчитися? Навчання - це відкриття. Я навчаюся чомусь за допомогою досвіду 
							(свою практичну діяльність я розпочала ще будучи на третьому курсі університету і до кінця свого життя не 
								планую завершувати)
						</p>
						<p className='info__paragraph'>
							А як я можу звертатися до Тебе? Розкажи про себе!
						</p>
						<Link to='/contacts'>
							<button className='contact-button btn btn-success'>Напиши мені ;)</button>
						</Link>
						<p className='info__heading'>Освіта:</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2714; </span>Український психотерапевтичний університет в рамках Української спілки психотерапевтів та Європейської 
							Асоціації Психотерапії. Спеціальність: психотерапевт, спеціалізація: гештальт психотерапевт;
						</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2714; </span>
							Львівський національний університет імені Івана Франка, філософський факультет, 
							спеціальність: психолог, спеціалізація: клінічна психологія (магістр психології).
						</p>
						<p className='info__heading'>Додаткова освіта:</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2714; </span>
						П’ятирічний міжнародний проект з групової психотерапії «Трускавецька школа психотерапії» 
						за напрямком гештальт психотерапія.
						</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2714; </span>
						Секція телефонного консультування Української Спілки Психотерапевтів: успішно 
						пройшла навчання та професійний відбір.
						</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2714; </span>
						Авторський трьохрічний навчальний курс: методи та форми супроводу клієнта в 
						арт-терапевтичному процесі з Вікторією Назаревич.
						</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2714; </span>
						Український психотерапевтичний університет. Навчальний проект: пропедевтика 
						психічних захворювань.
						</p>
						<p className='info__heading'>Головні цитати мого життя:</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2747; </span>
						«Переможи себе і виграєш тисячі битв».
						</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2747; </span>
						«Світ йде зсередини, не шукай його ззовні».
						</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2747; </span>
						«Тисячу свічок можна запалити від однієї, і життя тієї свічки не стане коротшим. 
						Щастя ніколи не зменшується, якщо їм ділитися».
						</p>
						<p className='info__paragraph'>
						<span role='img'>&#x2747; </span>
						«Одна з найбільш корисних життєвих навичок - це вміння швидко забувати все погане: 
						не зациклюватися на неприємностях, не жити образами, не упиватися роздратуванням, 
						чи не таїти злобу. Не варто тягти різний мотлох в свою душу».
						</p>
						<p className='info__heading'>Моє кредо:</p>
						<p className='info__paragraph'>
						«Ніколи не бійся щось змінити у своєму житті. Іноді це корисно. 
						Іноді необхідно, а іноді - до біса приємно»!
						</p>
					</div>
				</div>
			</div>
		)
	}
}



export default AboutMe