import React, { Component } from 'react'
import Header from '../headerComponent/header'
import { Link } from 'react-router-dom'
import Picture from '../../Assets/images/lotus-position.png'
import '../../Assets/css/nutrition.css'

class Nutrition extends Component {
	render() {
		return (
			<div>
				<Header />
				<div className='toggleButton'>
				<span>
				Тут є відео, а ще можна
					<Link to='/nutrition-articles'>
						<button className='btn btn-success'>читати статті</button>
					</Link>
					 про харчування
				</span>
				</div>
				<div className='nutrition__container'>
				<div className='video'>
					<p className='video__name animated slideInLeft'>
						Щось там про харчування
					</p>
					<div className="iframe-container">
						<iframe src="https://www.youtube.com/embed/fR3NxCR9z2U" frameborder="0" 
						allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
					</div>
					<hr />
					<p className='video__name animated slideInLeft'>
						Інтуїтивне харчування
					</p>
					<div className="iframe-container">
						<iframe src="https://www.youtube.com/embed/zZtGJH31dkY" frameborder="0" 
						allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
					</div>
					<hr />
					<p className='video__name animated slideInLeft'>
						Розлади харчової поведінки
					</p>
					<div className="iframe-container">
						<iframe src="https://www.youtube.com/embed/UEysOExcwrE" frameborder="0" 
						allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
					</div>
					<hr />
					<p className='video__name animated slideInLeft'>
						Довіряй своєму голоду
					</p>
					<div className="iframe-container">
						<iframe src="https://www.youtube.com/embed/Ssr2UDB9EWQ" frameborder="0" 
						allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
					</div>
				</div>
				<div className='photo-sitting animated fadeInDown'>
					<img src={Picture} alt='sitting-girl' />
				</div>
				<div className='text'>
					<div className='text__about'>
						<p className='paragraph'>
							Цей розділ присвячений найзаплунанішій та найнезрозумілішій темі Всесвіту! 
							Скільки людей, стільки й думок на тему харчування.
						</p>
						<p className='paragraph'>
							Твій образ тіла не вписується в образ соціальних тіл, 
							і Ти прагнеш розібратися з цим (не набивши нікому обличчя і 
							не заївши ці емоції та бажання) - тоді Ти вже у правильному місці, точніше на сайті!
						</p>
						<p className='important'>
							Оскільки один палець - не кулак!
						</p>
						<p className='paragraph'>
							Якщо хочеш йти швидко - йди сама, якщо хочеш йти далеко та назавжди з цим розібратися 
							– 
						</p>
						<p className='important'>
							ПІШЛИ РАЗОМ!
						</p>
						<p className='paragraph'>
							Усі мої розмови та дослідження про порушення харчової поведінки у людей та багаторічний власний досвід, 
							тепер зібрані воєдино на цьому сайті та у соц.мережах, де ми разом з Тобою поговоримо про наболіле. 
						</p>
						<p className='important'>
							Ми разом з тобою об'єднаємо увесь наш досвід та знайдемо той самий шлях рішення задачі!
						</p>
						<p className='paragraph'>
							«Ти ніколи не зможеш вирішити проблему, якщо збережеш те саме мислення і той самий підхід, 
							який привів тебе до цієї проблеми» (Альберт Ейнштейн)
						</p>
						<Link to='/contacts'>
							<button className='btn btn-success'>Напиши мені ;)</button>
						</Link>
					</div>
					<div className='text__articles'>
					</div>
				</div>
				</div>
			</div>
		)
	}
}
export default Nutrition