import React, { Component } from 'react'
import Header from '../headerComponent/header'
import '../../Assets/css/motivation.css'
import { Link } from 'react-router-dom'

class Motivation extends Component {
	search(event) {
		event.preventDefault()
	}
	render() {
		return (
			<div>
				<Header />
				<div className='motivation__container animated fadeIn'>
				<div className='video'>
				<p className='video__name animated slideInLeft'>
					Лекція Петерсона
				</p>
				<div class="iframe-container">
					<iframe src="https://www.youtube.com/embed/9CoL0ZuruWE" frameborder="0" 
					allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
				</div>
				<hr />
				<p className='video__name animated slideInLeft'>
					Марша Лайнен
				</p>
				<div class="iframe-container">
					<iframe src="https://www.youtube.com/embed/G08IYl2DAMg" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; 
					gyroscope; picture-in-picture" allowFullScreen></iframe>
				</div>
				<hr />
				<p className='video__name animated slideInLeft'>
					Лекція каналу Medcircle
				</p>
				<div class="iframe-container">
					<iframe src="https://www.youtube.com/embed/K784y_gwXUo" frameBorder="0" allow="accelerometer; autoplay;
					encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
				</div>
				<hr />
				<p className='video__name animated slideInLeft'>
					Лекція з Ted
				</p>
				<div class="iframe-container">
					<iframe src="https://www.youtube.com/embed/Cw2qD87KDHc" frameBorder="0" allow="accelerometer; autoplay; 
					encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
				</div>
				</div>
				<div className='text'>
					<p className='paragraph'>
						Чого ти хочеш досягти?
					</p>
					<p className='paragraph'>
						Що Ти повинна зробити для цього?
					</p>
					<p className='paragraph'>
						Чи зможеш Ти подолати себе і з усією рішучістю взятися за справу?
					</p>
					<p className='paragraph'>
						Що Ти отримаєш від прийнятого Тобою рішення?
					</p>
					<p className='important'>
						Це розділ де ми разом шукаємо можливості, а не причину!
					</p>
					<p className='paragraph'>
						Майбутнє за тими, хто розвивається. І робить це у всіх напрямках, адже важливо всебічно розвиватися, 
						а не якось однотипно чи ще гірше ніяк не розвиватися.
					</p>
					<p className='paragraph'>
						У Твоїх силах набагато більше ніж Ти думаєш. У Тебе є неймовірно величезний природний потенціал,
						 як у психологічній, так і в фізичній сфері.
					</p>
					<p className='important'>
						Потенціал потрібно тільки відкрити!
					</p>
					<p className='paragraph'>
						Сильні люди довели світові що можливо навіть те що вважають неможливим.
					</p>
					<p className='important'>
						Чим Ти гірша?
					</p>
					<p className='paragraph'>
						Межі можливого не існують! Ти сама ставиш собі рамки і визначаєш межу своїх можливостей!
					</p>
					<p className='important'>
						Сильні люди просто роблять !!!
					</p>
					<p className='paragraph'>
						Скажи - я хочу стати кращою, я стану кращою, я краща! Дія з думки, думка з бажання. Іди до кінця. 
						Пам'ятай – сильна  не та, хто не впала, а та, хто впала й піднялася! 
					</p>
					<p className='important'>
						Те, що не вбиває - робить Тебе сильнішою!
					</p>
					<p className='paragraph'>
						Невдача, поразка роблять Тебе кращою та сильнішою, якщо Ти встала та продовжуєш шлях! 
						Якщо впала - встань та йди! 
					</p>
					<div className='paragraph'>
						І найголовніше - <span>
						<form action="https://facebook.us19.list-manage.com/subscribe/post?u=6ee76c62ae5d32f355fad3587&amp;id=7090286fd3" method="post" id="mc-embedded-subscribe-form" 
						name="mc-embedded-subscribe-form" target="_blank" noValidate>
    					<input className="btn btn-success submit-button" type="submit" value="підпишись на розсилку!" name="subscribe" />
						</form>
						</span>
					</div>
					<hr />
					<div className='new-topic'>
				<p className='paragraph'>
					Запропонуй свою тему!
				</p>
				<p className='paragraph'>
					(І я її висвітлю)
				</p>
				<Link to='/contacts'>
					<button className='btn btn-success'>НОВА ТЕМА</button>
				</Link>
				<hr />
				<p className='paragraph'>
					Що ти шукаєш?
				</p>
				<form className="form-inline mr-auto">
  					<input className="form-control mr-sm-2" type="text" placeholder="Цікавинки" aria-label="Search" />
  					<button onClick={this.search} className="btn btn-outline-success btn-rounded btn-sm my-0" type="submit">Знайди!</button>
				</form>
			</div>
				</div>
			</div>
			</div>
		)
	}
}
export default Motivation