import React, { Component } from 'react'
import '../../Assets/css/menu.css'
import ListImage from '../../Assets/images/cherry.png'
import ListImage2 from '../../Assets/images/orange.png'
import ListImage3 from '../../Assets/images/watermelon.png'
import ListImage4 from '../../Assets/images/grapes.png'
import ExitImage from '../../Assets/images/exit.png'
import { Link } from 'react-router-dom'

class Menu extends Component {
	render() {
		return (
			<div className='menu-flex animated fadeIn'>
			<nav>
			<ul>
				<li className="navItem">
				<Link to='/activity'>
					<p className="question">ЩО РОБИМО</p>
					<p className="answer">тренінги, консультації</p>
				</Link>
					<img className='menuFruit animated bounceInDown' src={ListImage} alt="bullet point" />
				</li>
				<li className="navItem">
				<Link to='/motivation'>
					<p className="question">ЩО МОТИВАЦІЯ</p>
					<p className="answer">мотиваційний запал для життя</p>
				</Link>
					<img className='menuFruit animated bounceInDown' src={ListImage2} alt="bullet point" />
				</li>
				<li className="navItem">
				<Link to='/nutrition'>
					<p className="question">ЩО ХАРЧУВАННЯ</p>
					<p className="answer">блог про харчування та спростовування міфів</p>
				</Link>
					<img className='menuFruit animated bounceInDown' src={ListImage3} alt="bullet point" />
				</li>
				<li className="navItem">
				<Link to='/aboutme'>
					<p className="question">ЩО ПРО МЕНЕ</p>
					<p className="answer">хто я така та чим я займаюся</p>
				</Link>
					<img className='menuFruit animated bounceInDown' src={ListImage4} alt="bullet point" />
				</li>
				<li className="navItem">
				<Link to='/contacts'>
					<p className="question">ЩО КОНТАКТИ</p>
					<p className="answer">побажання, пропозиції, просто привіт</p>
				</Link>
				</li>
				</ul>
			</nav>
			<div className='exitButton'>
			<Link to='/'>
				<img className='exitButtonImage animated slideInRight' src={ExitImage} alt="go back" />
			</Link>
			</div>
			</div>
		)
	}
}
export default Menu