import React, { Component } from 'react';
import Logo from '../../Assets/images/logo.png'
import Menu from '../../Assets/images/menu-button.png'
import Mail from '../../Assets/images/message.png'
import Search from '../../Assets/images/search.png'
import { Link } from 'react-router-dom'
import Tilt from 'react-tilt'
import '../../Assets/css/header.css'

class Header extends Component {
	render() {
		return (
			<div className='header'>
				<Link to='/menu'>
				<Tilt id="logo" options={{ max : 80 }} style={{ width: 80, height: 'auto' }} >
					<img className="logo animated slideInDown" src={Logo} alt="logo"/>
				</Tilt>
				</Link>
				<div className="navButtons animated slideInRight">
					<Link to='/menu'>
						<img className="navButton" src={Menu} alt="menu"/>
					</Link>
					<Link to='/contacts'>
						<img className="navButtonSquare navButton" src={Mail} alt="mail-address"/>
					</Link>
					<img className="navButtonSquare navButton" src={Search} alt="menu"/>
				</div>
			</div>
		)
	}
}
export default Header;